.change-img-myVacancy{
    font-size: 14px;
    margin-left: 15px;
    margin-top: 5px;
}
.orange-link-myVacancy{
    color:#f68c06;
}
.orange-link-myVacancy:hover{
    color: #f26522;
    cursor: pointer;
}
.orange-link-myVacancy:focus{
    color: #f26522;
}
.position-myVacancy{
    font-family: SegoeUIRegular;
    font-size: 18px;
    color: #f26522;
    margin: 0;
    margin-top: 5px;
}
