    color: #f68c06;
}
.orangColor-myVacancy:hover{
    color: #f26522;
}
.orangColor-myVacancy:focus{
    color: #f26522;
}
.orangColor-myVacancy-name{
    color: #797979;
    font-size: 24px;
}
.orangColor-myVacancy-name:hover{
    color: #f68c06;
}
.orangColor-myVacancy-name:focus{
    color: #f26522;
}
.nameCompany-myVacancy{
    color: #f26522;
    font-size: 18px;
}
.nameCompany-myVacancy:hover{
    color: #f26522;
}
.nameCompany-myVacancy:focus{
     color: #f26522;
 }
.city-myVacancy{
    color: #797979;
}
.city-myVacancy:hover{
    color: #f68c06;
}
.city-myVacancy:focus{
    color: #f68c06;
}
.price-myVacancy{
    color: #CD2626;
    font-size: 18px;
    font-weight: bold;
    margin: 0px;
}
